@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

:root {
    --primary-white: #ffffff;
    --primary-light-grey: #f4f4f4;
    --primary-grey: #ccd1d9;
    --primary-dark-grey: #666666;
    --primary-orange: #f9ab00;
    --primary-dark: rgb(52,58,64);
    --primary-red: #ef4035;
    --primary-hover-red: #f33501;
    --primary-black: #000000;
    --primary-transparent-black: rgba(0, 0, 0, 0.7);
    --primary-green: green;
}

body {
    font-family: "Roboto Condensed", sans-serif !important;
}
  /* Navbar */
nav {
    z-index: 2;
}

.logo {
    width: 16.25rem;
}

/* Menu links */
nav a.nav-link {
    letter-spacing: 0.0375rem;
    cursor: pointer;
}

nav ul li {
    text-transform: uppercase;
}

.navbar-nav > li > a {
    margin-right: 0.75rem;
    margin-left: 0.75rem;
    color: var(--primary-white) !important;
    border-bottom: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
    font-size: 4rem !important;
    color: var(--primary-white) !important;
    max-width: 17.25rem;
    padding: 0 !important;
    height: 6rem !important;
}



@media(max-width: 768px) {
    .navbar-brand {
        width: 7rem;
    }
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
    border-bottom: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
    border-bottom: 0.1875rem solid var(--primary-orange);
}

/* Header */
.header-wrapper {
    position: relative;
    background:linear-gradient(to right, rgba(0,0,0,0.9), rgba(0,0,0,0.5)), url(./images/bg6.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
}

.main-info {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: "Roboto Condensed", sans-serif;
    z-index: 1;
}

.main-info h1 {
    color: var(--primary-orange);
    text-transform: uppercase;
}

@media(max-width: 768px) {
    .main-info h1 {
        font-size: 1.7rem !important;
    }
}

/* typed text */
.typed-text {
    font-size: 2rem;
    color: var(--primary-white);
}

@media(max-width: 768px) {
    .typed-text {
        font-size: 1.3rem !important;
    }
}

/* header main offer button */
.btn-main-offer {
    border: 1px solid var(--primary-red);
    text-transform: uppercase;
    border-radius: 0;
    padding: 0.625rem 0.75rem 0.75rem 0.75rem;
    color: var(--primary-white);
    margin: 2rem;
}

.btn-main-offer:hover {
    text-decoration: none;
    background-color: var(--primary-hover-red);
    color: var(--primary-white);
    transition: .2s ease-in-out;

}

/* particles js */
.tsparticles-canvas-el {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}

/* @media(max-width: 768px) {
    .particles-canvas {
        display: none;
    }
} */

/* About me */
.photo-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-img {
    width: 16rem;
    border-radius: 50%;
    padding: .3rem;
    border: 1px solid var(--primary-orange);
}

.about-heading {
    color: var(--primary-orange);
    text-transform: uppercase;
}

@media(max-width: 468px) {
    .profile-img {
        width: 10rem;
    }
}

/************************ Skills *****************************/
.skills {
    text-align: center;
    background: var(--primary-grey);
    margin-bottom: 0 !important;
}

.skills h1 {
    color: var(--primary-orange);
}

.icons p {
    margin-bottom: 0;
}

.icon {
    margin-right: 1rem;
    margin-left: 1rem;
    margin-bottom: 5rem;
}

.icons p {
    margin-bottom: 0;
}


/* ************************* portfolio **************************** */
.portfolio-wrapper {
    background: var(--primary-light-grey);
    padding: 3rem 0;
}

.portfolio-wrapper h1 {
    color: var(--primary-orange);
}

.portfolio-image {
    width: 15rem;
    border: 1px solid var(--primary-orange);
    padding: 0 0.5rem;
}

.portfolio-image-box {
    position: relative;
    margin: 1rem;
}

/* OVER FLOW BOX */
.overflow {
    position: absolute;
    opacity: 0;
    background: var(--primary-black);
    width: 15rem;
    height: 100%;
    top: 0;
    border: 1px solid var(--primary-red);
    cursor: pointer;
}

.portfolio-image-box:hover .overflow {
    opacity: 60%;
    transition: 0.3s ease-in-out;
}

.portfolio-icon {
    position: absolute;
    opacity: 0;
    margin-left: -8rem;
    margin-top: 3.6rem;
    color: var(--primary-orange);
    font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
    opacity: 70%;
    transition: 0.3s ease-in-out;
    cursor: pointer;
}

.popupbox-wrapper {
    margin-top: 7rem;
}

/*  */
.portfolio-image-popupbox {
    width: 40rem;
    padding: 0 0.5rem;
}

.utilize {
    display: flex;
    padding-top: 0.5rem;
}

.utilize h4 {
    background: var(--primary-orange);
    margin-right: 2rem;
    padding: 0.5rem;
    font-size: 1rem;
    border-radius: 5px;
}

.hyper-link {
    cursor: pointer;
    color: var(--primary-orange);
}

.hyper-link:hover {
    color: var(--primary-red);
}
/* media */
@media(max-width: 768px) {
    .portfolio-image-popupbox {
        width: 100%;
    }
}

@media(max-height: 570px) {
    .popupbox-wrapper {
        height: 100%;
    }

    .portfolio-image-popupbox {
        width: 50%;
    }
}

@media(max-height: 280px) {
    .popupbox-wrapper {
        height: 90%;
    }

    .portfolio-image-popupbox {
        width: 30%;
    }
}

/* ************************* contacts **************************** */
.contacts {
    background: var(--primary-dark);
    padding: 3rem 0;
}

.contacts h1 {
    color: var(--primary-orange);
    padding: 1.3rem 0;
    text-transform: uppercase;
}

.contacts p {
    color: var(--primary-light-grey);
    font-size: 1.2rem;
}

.success-message {
    font-weight: 900;
    color: var(--primary-green);
}

/* INPUT */
.contacts input, .contacts textarea {
    width: 100%;
    box-sizing: border-box;
    background: none;
    resize: none;
    border: 0;
    border-bottom: 0.125rem solid var(--primary-dark-grey);
    border-radius: 0;
    color: var(--primary-orange);
    outline: 0 !important;
    box-shadow: none !important;
    margin-bottom: 1rem;
    padding-left: 0 !important;
}

.contacts .container {
    padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
    background: transparent;
    color: var(--primary-orange);
    border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
    height: 12.2rem !important;
}

.contact-btn {
    margin: 1rem 0 0 0;
    width: 100%;
    background: none;
}

.line {
    position: relative;
    top: -0.8rem;
    width: 0;
    height: 0.125rem;
    background: var(--primary-orange);
    display: inline-block;
}

input:focus+.line, textarea:focus+.line {
    width: 100%;
    transition: width 0.5s ease-in-out;
}

input.form-control {
    margin: -0.3rem 0;
}

textarea.form-control {
    margin: -0.3rem 0;
}

.error-message {
    color: var(--primary-white);
    background: var(--primary-hover-red);
    text-align: left;
}

/* media */
@media(max-width: 768px) {
    .contacts {
        overflow-x: hidden;
    }

    .contacts .container {
        padding: 0 1rem;
    }

    .contacts p {
        padding: 0 0.4rem;
    }
}

